import React, { Component } from 'react';
import { Route } from 'react-router';
import { Home } from './components/Home';
import { Layout } from "./Layout";
import "./index.css";
import ProjectsPage from './components/projects/ProjectsPage';
import BactoID from './components/projects/singleProject/BactoID';
import AboutPage from './components/about/AboutPage';
import UnitedAngels from './components/projects/singleProject/UnitedAngels';
export default class App extends Component {
  static displayName = App.name;

  render () {
    return (
      <Layout>
        <Route exact path='/' component={Home} />
        <Route exact path='/projects' component={ProjectsPage} />
        <Route exact path='/projects/bactoid' component={BactoID} />
        <Route exact path='/projects/united-angels' component={UnitedAngels} />
        <Route exact path='/about' component={AboutPage} />
      </Layout>
    );
  }
}
